body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	box-sizing: border-box !important;
	color: black !important;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	line-height: 1.5 !important;
	-webkit-font-smoothing: antialiased !important;
	text-rendering: optimizeLegibility;
}

#quote-box {
	width: 70%;
	max-width: 700px;
	height: 40%;
	padding: 30px;
	margin-top: 20px;
	display: flex;
	margin: 0 auto;
	flex-direction: column;
	border-radius: 10px;
	background-color: white;
	box-shadow: 0 5px 20px 5px rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19) !important
}

#quote-box hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 0px;
  padding: 0;
}

.QuoteBox-quote {
	padding: 10px;
	display: flex;
	flex: 3 1;
	flex-direction: column;
}

.QuoteBox-quote #text {
	font-size: 1.6em;
}

.QuoteBox-quote #author {
	font-size: 1.4em;
	text-align: right;
}

.QuoteBox-controls {
	display: flex;
	flex: 1 1;
	padding: 15px;
	margin-top: 20px;
	align-items: center;
	justify-content: space-evenly;
}

.QuoteBox-controls #new-quote {
	/*text-shadow: */
	color: white;
	background-color: #d8590e;
  	padding: 10px 15px;
	cursor: pointer;
	border: 2px solid black;
	border-radius: 10px;
	font-weight: bold;
	font-size: 1.2em;
	transition: color 0.5s ease;
	transition: background-color 0.5s ease;
}

.QuoteBox-controls #new-quote:hover {
	color: #d8590e;
	background-color: white;
}

.QuoteBox-controls #new-quote:focus {
	outline: none;
}

.tweet-button {
	/*text-shadow: */
	color: white;
	background-color: #1b95e0;
  	padding: 10px 15px;
	cursor: pointer;
	border: 2px solid black;
	border-radius: 10px;
	font-weight: bold;
	font-size: 1.2em;
	transition: color 0.5s ease;
	transition: background-color 0.5s ease;
	outline: none;
	text-decoration: none;
}
body {
	background: linear-gradient(270deg, #00ff2b, #0081ff);
	background-size: 400% 400%;
	-webkit-animation: Gradient-transition 5s ease;
	        animation: Gradient-transition 5s ease;
}

@-webkit-keyframes Gradient-transition { 
	0% {background-position:0% 50%}
	50% {background-position:100% 50%}
	100% {background-position:0% 50%}
}

@keyframes Gradient-transition { 
	0% {background-position:0% 50%}
	50% {background-position:100% 50%}
	100% {background-position:0% 50%}
}

.App {
	text-align: center;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 50px;
}

.App-header {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	align-content: center;
	background-color: #222;
	height: 150px;
	padding: 20px;
	margin-bottom: 50px;
	color: white;
}

.App-react-app {
	display: flex;
	align-self: center;
}

.App-title {
	font-size: 1.5em;
}

.App-intro {
	font-size: large;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
